@charset "utf-8";

.todo_item {
  padding: 1rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  line-height: 40px;
  position: relative
}

.todo_item:hover {
  background-color: #ccc;
}

/*  */
.todo_item:hover .check_mark {
  opacity: 1;
}

/*  */
.todo_item+.todo_item {
  border-top: 1px solid #f1f3f5;
}

/*  */
.remove {
  /* opacity: 0; */
  font-weight: bold;
  color: #f00;
  margin-right: 1rem;
}

.todo_text {
  flex: 1;
  word-break: break-all;

}

/* 해당 글목록 클릭시 글자서식이 흐리고 가운데 줄 표시하도록 함 */
.checked {
  text-decoration: line-through;
  color: #adb5db;
}

.check_mark {
  font-size: 1rem;
  line-height: normal;
  color: #fff;
  background: rgb(84, 130, 199);
  font-weight: bold;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 2px 5px;
  position: absolute;
  right: 70px;
  top: 20px;
  opacity: 0;
}

.checked .check_mark {
  background: #fff;
  color: #ccc;
}

.del_btn {
  font-size: 1rem;
  line-height: normal;
  color: #fff;
  background: rgb(238, 80, 80);
  font-weight: bold;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 2px 5px;
  ;
  position: absolute;
  right: 20px;
  top: 20px;
}