@charset "utf-8";
/* 입력폼 서식 */

.form {
  display: flex;
  padding: 1rem;
}

.form input {
  flex: 1;
  font-size: 1.25rem;
  outline: none;
  border: none;
  border-bottom: 1px solid #c5f6fa;
}

/* 추가 입력버튼서식 */
.create_btn {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  background: #22b8cf;
  border-radius: 3px;
  color: #fff;
  font-weight: 600;
  border: none;
  cursor: pointer;
}

.create_btn:hover {
  background: #3bc9bd;
}